<template>
  <div class="box">
    <div class="box_zhanshi">
      <p class="login_image">
         <img src="../assets/xigua.png"/>
      </p>
      <img src="../assets/lg_line.png"/>
    	<div style="margin-top: 80px;">
    		<p style="padding-left: 70px;margin-bottom: 65px;"><img src="../assets/title.png"/></p>
    		<div class="login_text">
    		  <p><img src="../assets/one.png"/></p>
    		  <p><img src="../assets/two.png"/></p>
    		  <p><img src="../assets/three.png"/></p>
    		</div>
    	</div>
    </div>
    <div class="login">
      <p style="margin-top: 90px;text-align: center;">
        <img src="../assets/greet.png" alt=""/>
      </p>

      <div class="submit">
        <p>
            <img src="../assets/users.png" class="login_img"/>
           <input v-model="username" placeholder="请输入账号" />
        </p>
         <p>
          <img src="../assets/cipher.png" class="login_img"/>
          <input v-model="password" type="password" placeholder="请输入密码" @keydown.enter="submit"/>
         </p>
          <button @click="submit" class="login_button">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
  import md5 from 'js-md5'
  export default {
    data() {
      return {
        errorUserName:'',
        errorPassWord:'',
        username:"",
        password:''
      }
    },
    methods: {
      out(){
        if (this.username.trim()){
          this.errorUserName='';
        }else {
          this.errorUserName='用户名不能为空'
        }
        if(this.password.trim()){
          this.errorPassWord=''
        }else{
          this.errorPassWord='密码不能为空'
        }
      },
      input(){
        this.out();
      },
      submit() {
        this.out();
        this.post('system/Login/index',{
          username:this.username,
          password:this.password
        }).then(res => {
          if(res.data.code===10000){
            this.$router.push('/home');
            sessionStorage.setItem("user",res.data.admin.is_agency_user);
          }else{
            const content = res.data.msg_test
                this.$Modal.error({
                    title: "提交失败",
                    content: content
                });
          }
        })
      },
    },
    mounted() {
      let fluc = document.getElementsByClassName('box_zhanshi')[0]
      fluc.animate([{
       left:'0'
      },{
        left:"5%"
      },
      ],2000)
      let login = document.getElementsByClassName("login")[0]
      login .animate([{
        right:"0"
      },{
        right:'10%'
      }],2000)
    }
  }
</script>

<style scoped>
  .box {
    position: fixed;
    width: 100%;
    height: 100%;
    background: url(../assets/spaner.png) no-repeat center;
    background-size:100% 100%;
    display: flex;
    justify-content:space-between;
    overflow-y: auto;
  }
  .login{
    flex: 0 0 420px;
    margin-top: 180px;
    margin-right: 255px;
    width:420px;
    height:542px;
    background: #F6F7FA;
    border-radius: 5px;
    background-size: cover;
  }
  .submit{
   margin-left: 55px;
   margin-top:68px;

  }
  .submit>p>input{
    width: 300px;
    height: 40px;
    margin-bottom: 30px;
    border-bottom: 1px solid #E0E4EB;
    background: none;
    padding-left: 50px;
    font-size: 18px;
  }
  .submit>p{
    position: relative;
  }
  .login_img{
    display: inline-block;
    width: 25px;
    position: absolute;
    left: 4px;
    top: 5px;
  }
  .login_button{
    display: block;
    margin-top: 50px;
    width: 305px;
    height: 45px;
    border-radius:20px;
    font-size: 16px;
    border: none;
    background: #5584FF;
    color: white;
    cursor: pointer;
    outline: none;
  }
  .box_zhanshi{
   flex: 0 0 500px;
   margin-top: 50px;
   margin-left: 110px;

  }
  .login_text{
    padding-left: 65px;
    display: flex;
  }
   .login_text>p{
     margin-right: 50px;
   }
  input::-webkit-input-placeholder

  {
    color: #B7BBC3;
    font-size: 18px;
  }
    input:-ms-input-placeholder

  {
    color: #B7BBC3;
    font-size: 18px;
  }
   input:-moz-placeholder

  {
    color: #B7BBC3;
    font-size: 18px;
  }
   input::-moz-placeholder
  {
    color: #B7BBC3;
    font-size: 18px;
  }
</style>



